import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { logoutUser } from '../../actions/authActions'
import Navbar from '../partials/Navbar'
import Sidebar from '../partials/Sidebar'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faList } from '@fortawesome/free-solid-svg-icons/faList'
import { Link } from 'react-router-dom'
import { faUserAlt } from '@fortawesome/free-solid-svg-icons/faUserAlt'

class Billing extends Component {
  onLogoutClick = (e) => {
    e.preventDefault()
    this.props.logoutUser()
  }

  render() {
    //const { user } = this.props.auth;
    return (
      <div>
        <Navbar />
        <div className="d-flex" id="wrapper">
          <Sidebar />
          <div id="page-content-wrapper">
            <div className="container-fluid">
              <button className="btn btn-link mt-2" id="menu-toggle">
                <FontAwesomeIcon icon={faList} />
              </button>

              <div className="col-sm-3 p-sm-2">
                <div className="card bg-secondary text-white shadow-lg">
                  <div className="card-body">
                    <h5 className="card-title">Special title treatment</h5>
                    <p className="card-text">
                      With supporting text below as a natural lead-in to
                      additional content.
                    </p>
                    <a href="#" className="btn btn-light">
                      Go somewhere
                    </a>
                  </div>
                </div>
              </div>
              <div className="col-sm-3 p-sm-2">
                <div className="card bg-info text-white shadow-lg">
                  <div className="card-body">
                    <h5 className="card-title">Special title treatment</h5>
                    <p className="card-text">
                      With supporting text below as a natural lead-in to
                      additional content.
                    </p>
                    <a href="#" className="btn btn-light">
                      Go somewhere
                    </a>
                  </div>
                </div>
              </div>
              <div className="col-sm-3 p-sm-2">
                <div className="card bg-dark text-white shadow-lg">
                  <div className="card-body">
                    <h5 className="card-title">Special title treatment</h5>
                    <p className="card-text">
                      With supporting text below as a natural lead-in to
                      additional content.
                    </p>
                    <a href="#" className="btn btn-light">
                      Go somewhere
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

Billing.propTypes = {
  logoutUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
}

const mapStateToProps = (state) => ({
  auth: state.auth,
})

export default connect(mapStateToProps, { logoutUser })(Billing)
